body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  text-align: center;
  padding: 20px;
}

header img {
  max-width: 200px;
  border-radius: 100px;
  padding: 10px;
  border: solid 1px #000;
}

@media (min-width: 768px) {
  header img {
    max-width: 300px;
    border-radius: 150px;
  }
}

section {
  padding: 20px;
  margin: 0 auto;
  max-width: 700px;
}

section.contact {
  text-align: center;
  padding: 20px;
}

section.contact a {
  display: inline-block;
  padding: 15px 30px;
  color: #fff;
  background: #000;
  text-decoration: none;
}

footer {
  padding: 20px;
  text-align: center;
}

footer i {
  color: #000;
}

footer a {
  display: inline-block;
  padding: 10px;
}

.App {
  font-family: 'Roboto', sans-serif;
}

.App * {
  box-sizing: border-box;
}

